.step-success-row__back-to-main {
  border: $_1px solid $secondary-color;
  color: $secondary-color;
  padding: $_12px;
  font-size: $_14px;
  max-width: 12.375rem;
}

// IOS support to prevent it from zooming when User clicks on the inputs

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select:focus,
  textarea:focus,
  input:focus {
    font-size: 16px;
    background: #fff;
  }
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select,
  textarea,
  input {
    font-size: 16px;
  }
}

